$darker-blue: #0B0C10;
$dark-blue: #1F2833;
$grey: #C5C6C7;
$light-blue: #45A29E;
$lighter-blue: #66FCF1;

$dark-spotify: #222222;
$tomato: #eb583a;
$true-tomato: #b85341;
$dark-tomato: #963c2e;
$green: #81ad98;
$dark-bege: #45A29E;
$bege: #66FCF1;


html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
  font-size: 1.2vh;
  background-color: whitesmoke;
  // display: flex;
  flex-direction: row;
  font-family: "Helvetica Neue",sans-serif;
}

.app {
  height: 100%;
  width: 100%;
}

.canvas {
  height: 100%;
  width: 100%;
  // filter: grayscale(1);
  pointer-events: none;
}

.canvas-group {
  height: 100%;
  display: flex;
  flex-direction: row;
}

.subject-canvas {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  flex-shrink: 0;
  -webkit-touch-callout: none;
  background-color: whitesmoke;
  overflow: hidden;
}

.who-am-i {
  background-color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.web-dev {
  background-color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.game-dev {
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.black-bg {
  background-color: rgba(black, 0.3);
}

.blur-bg {
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

.card {
  padding: 1rem;
  border-radius: 1rem;
}

.container {
  display: flex;
  flex-direction: row;
  max-width: 80vw;
  max-height: 80vh;
  margin-bottom: 10rem;
  z-index: 1;
}

.container-wd {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100rem;
  max-width: 80vw;
  max-height: 80vh;
  margin-bottom: 10rem;
  z-index: 1;
}

.container-gd {
  display: flex;
  flex-direction: column;
  max-width: 90rem;
  max-height: 80vh;
  margin-bottom: 8rem;
  z-index: 1;
}

.left-container {
  // flex: 1;
  width: min-content;
  display: flex;
  align-items: center;
}

.three-containers {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 3rem;
  background-color: rgba(white, 0.1);
  pointer-events: stroke;
  // width: 40rem;
}

.icon-container {
  background-color: rgba(black, 0.1);
  min-height: 16rem;
  height: 16rem;
  width: 16rem;
  border-radius: 10rem;
  margin-bottom: 2rem;
  backdrop-filter: blur(4px);
  align-self: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.column-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 2rem;
  text-align: center;
}

.column-about {
  font-size: 2rem;
  font-weight: thin;
  margin-bottom: 1rem;
}

.column-about-small {
  font-size: 1rem;
  font-weight: thin;
}

.text-container {
  display: flex;
  flex-direction: row;
}

.name-text {
  font-size: 6rem;
  font-weight: bold;
  margin-bottom: 2rem;
}

.title-text-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8rem;
}

.title-text {
  font-size: 4rem;
  font-weight: bold;
}

.about-text {
  font-size: 1.8rem;
  font-weight: thin;
  margin-bottom: 1rem;
}

.about-text-small {
  font-size: 1.2rem;
  font-weight: thin;
}

.justify {
  text-align: justify;
}

.about-list {
  margin: 0 2rem;
}

.about-list-item {
  margin: 2rem 0;
}

.script-bf-box {
  width: 5px;
  height: 300px;
  background: $dark-tomato;
  margin: 5px;
  flex-shrink: 0;
  will-change: transform;
}

.particle {
  position: absolute;
  width: 5px;
  height: 5px;
  background: $dark-tomato;
  flex-shrink: 0;
  will-change: transform;
}

.button {
  width: 14vw;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  background-color: rgba(whitesmoke, 0.5);
  // mix-blend-mode: difference;
}

.button-text {
  color: black;
  // mix-blend-mode: difference;
  // filter: invert(1);
  text-align: center;
  font-size: 1.2rem;
  letter-spacing: 0.3rem;
}

.button:hover {
  cursor: pointer;
  background-color: rgba(white, 1);
}

.button-container {
  width: 80vw;
  // padding: 0px 10vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.footer {
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 8rem;
}

.video {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(0.5);
}

.link-icon-group {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.link-icon {
  margin-right: 1rem;
  cursor: pointer;
}

.selectable {
  -moz-user-select: text;
  -khtml-user-select: text;
  -webkit-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

.clickable {
  cursor: pointer;
}

.trails-main {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.trails-text {
  color: black;
  font-size: 8em;
  font-weight: 800;
  letter-spacing: -9px;
  will-change: transform, opacity;
  overflow: hidden;
}

.trails-text > div {
  overflow: hidden;
}


.translate[sceneId='0'] {
  transform: translateX(0em);
  transition: transform 0.5s ease-in-out;
}
.translate[sceneId='1'] {
  transform: translateX(-100vw);
  transition: transform 0.5s ease-in-out;
}
.translate[sceneId='2'] {
  transform: translateX(-200vw);
  transition: transform 0.5s ease-in-out;
}
.translate[sceneId='3'] {
  transform: translateX(-300vw);
  transition: transform 0.5s ease-in-out;
}
.translate[sceneId='4'] {
  transform: translateX(-400vw);
  transition: transform 0.5s ease-in-out;
}

@media only screen and (max-width: 1100px) {
  html,
  body,
  #root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    overflow: hidden;
    font-size: 12px;
    background-color: whitesmoke;
    // display: flex;
    flex-direction: row;
    font-family: "Helvetica Neue",sans-serif;
  }

  .subject-canvas {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    flex-shrink: 0;
    -webkit-touch-callout: none;
    background-color: whitesmoke;
    overflow: auto;
  }

  .container-wd {
    display: block;
    flex-direction: column;
    justify-content: flex-start;
    width: 100rem;
    max-width: 80vw;
    max-height: 100vh;
    margin-bottom: 0rem;
    padding-top: 10rem;
    z-index: 1;
  }

  .three-containers {
    padding: 2rem 0;
  }

  .name-text {
    font-size: 5rem;
    font-weight: bold;
    margin-bottom: 2rem;
  }

  .title-text-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
  }

  .text-container {
    display: flex;
    flex-direction: column;
  }

  .footer {
    width: 100%;
    display: flex;
    position: absolute;
    bottom: 0;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 3rem;
  }

  .button {
    // isolation: isolate;
    width: auto;
    padding: 1rem 1rem;
  }

  .button-text {
    font-size: 1rem;
  }
}